ar b = function(){}\n            console.log(b);       // function b() {}\n            function d(){}\n          }\n          fn(1);\n          // 解析过程\n          1) AO {}\n          2) AO {\n            a: undefined,\n            b: undefined\n          }\n          3) AO {\n            a: 1, //实参为1\n            b: undefined\n          }\n          4) AO {\n            a: function a() {},\n            b: undefined,\n            d: function d() {}\n          }\n          // 执行fn(1)后\n          AO {\n            a: 123,\n            b: function b() {},\n            d: function d() {}\n          }\n\n      eg2:\n          function test(a,b){\n            console.log(a);   // 1\n            c = 0;\n            var c;\n            a = 3;\n            b = 2;\n            console.log(b);  // 2\n            function b() {}\n            function d() {}\n            console.log(b);  // 2\n          }\n          test(1)\n          // 解析过程\n          1) AO {}\n          2) AO {\n            a: undefined,\n            b: undefined,\n            c: undefined\n          }\n          3) AO {\n            a: 1, //实参为1\n            b: undefined,\n            c: undefined\n          }\n          4) AO {\n            a: 1,\n            b: function b() {},\n            c: undefined,\n            d: function d() {}\n          }\n          // 执行test(1)后\n          AO {\n            a: 3,\n            b: 2,\n            c: 0,\n            d: function d() {}\n          }\n\n      eg3:\n          function test(a,b){\n            console.log(a);      // function a() {}\n            console.log(b);      // undefined\n            var b = 234;\n            console.log(b);      // 234\n            a = 123;\n            console.log(a);      // 123\n            function a() {}\n            var a;\n            b = 234;              \n            var b = function () {}\n            console.log(a);     // 123\n            console.log(b);     // \n            function () {}\n          }\n          tes