 10;    \n          console.log(c);    // 234\n\n          // 解析过程\n          GO {\n            test: test(){****},\n            a : undefined\n          }\n          test() ==>\n          AO {\n            b : undefined\n          }\n          GO {\n            test: test(){****},\n            a : undefined ,\n            c : 234\n          }\n\n      eg5:\n          a = 100;   // go中a = 100;\n          function demo(e) {\n            function e(){}      // AO中e为function  \n            arguments[0] = 2;   // 映射关系,AO中e = 2\n            console.log(e);     // 2\n            if(a){              // a为undefiend,代码块便不执行\n              var b = 123;\n              c = 10;\n            }\n            var c;\n            a = 10;             // AO中 a = 10\n            var a;\n            console.log(b);     // undefined\n            f = 123;            // GO中f = 123\n            console.log(c);     // undefined\n            console.log(a);     // 10\n          }\n          var a;\n          demo(1);\n          console.log(a);       // 100\n          console.log(f);       // 123\n    ")]),_vm._v("\n  ")])])}
